import React, { Component } from 'react';
import WorkoutDay from './WorkoutDay';

class Workout extends Component {
    state = {  }
    render() { 
        let toRender = []
        for(let i = 0; i < 6; i++) {
            toRender.push(<div style={{margin: "15px"}}><WorkoutDay workout={this.props.workout} day={i}/></div>)
        }
        return ( 
            <div class="minHeight doodle">
                <div class="container">
                    {toRender}
                </div>
                
            </div> 
        );
    }
}
 
export default Workout;